.castContainer {
  background-color: #f9f9f9;
  padding: 20px;
  border-radius: 4px;
  margin: 20px 0;
}

.castList {
  list-style: none;
  padding: 0;
}

.castItem {
  display: flex;
  margin: 10px 0;
}

.actorImage {
  width: 80px;
  height: 120px;
  object-fit: cover;
  margin-right: 10px;
  border-radius: 4px;
}

.actorImage:hover {
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
}

.actorImage:focus {
  outline: none;
}

.actorName {
  font-weight: bold;
  font-size: 1.2rem;
}

.actorCharacter {
  font-size: 1rem;
}

.loading {
  text-align: center;
  font-size: 1.5rem;
  margin: 20px;
}